.situatie {

	.accordion {
		margin-top: 1rem;
	}

	&-storyOrImage {

		&.solo {
			height: 12rem;
			width: 17rem;
			overflow: hidden;
			margin-bottom: 1rem;
		}

		@include mqLesserThan(mediumLarge) {
			width: calc(100% - 2rem);
			margin-bottom: 1rem;
		}
	}

	.pageIntro {
		@include mqLesserThan(mediumLarge) {
			width: calc(100% - 2rem);
		}
	}
}