.cookieAlert {
	width: 100%;
	position: fixed;
	left: 0;
	bottom: 0;
	@include zIndex(dialog);

	&-container {
		padding: {
			top: 1rem;
			bottom: 1rem;
		}
		display: flex;
		position: relative;
		background: getColor(mvp toast);
		color: getColor(mvp text);
		border-top: 3px solid getColor(donkerblauw default);
		box-shadow: 0 -3px 19px 0 rgba(0,0,0,0.09);
	}

	.contentContainer {

		@include mqGreaterThan(medium) {
			width: 100%; // IE11
			display: flex;
		}
	}

	&-buttonContainer {
		margin-top: 1rem;
		margin-bottom: 0.5rem;
		flex: 1;
		text-align: center;

		@include mqGreaterThan(medium) {
			margin-top: 0;
		}
	}

	&-button {
		background: getColor(violet default);
		@include hoverEtc {
			background: getColor(violet darker);
		}
	}

	&-text {
		margin: 0;
		padding-right: 3rem;
		flex: 2;

		@include mqGreaterThan(medium) {
			margin-right: 0;
		}
	}

	&-readMore {
		color: getColor(hemelblauw default);

		@include hoverEtc {
			border-bottom: 1px solid;
			color: getColor(donkerblauw default);
		}
	}

	&-close {
		right: 1rem;

		@include mqGreaterThan(large) {
			right: 2rem;
		}
	}
}