$closeButtonForegroundColor: getColor(hemelblauw darkest) !default;
$closeButtonBackgroundColor: getColor(wit) !default;
$closeButtonOnDarkColor: getColor(wit) !default;
$closeButtonAlternativeBackgroundColor: getColor(hemelblauw lighter) !default;

$closeButtonSmallSize: 2rem;
$closeButtonLargeSize: 3.16rem;

.close {
	transition: transform 0.2s ease-in-out;
	padding: 0;
	background: none;
	border: none;
	width: $closeButtonSmallSize;
	height: $closeButtonSmallSize;
	padding: ($menuIconSizePaddingRatioSmall * $closeButtonSmallSize);
	background: $closeButtonBackgroundColor;
	color: $closeButtonForegroundColor;
	fill: $closeButtonForegroundColor;
	overflow: hidden;

	@include mqGreaterThan(small) {
		width: $closeButtonLargeSize;
		height: $closeButtonLargeSize;
		padding: ($menuIconSizePaddingRatioLarge * $closeButtonLargeSize);
	}

	@include hoverEtc {
		transform: scale(1.1);
		background: $closeButtonBackgroundColor;
		color: $closeButtonForegroundColor;
		fill: $closeButtonForegroundColor;
	}

	&--onDark {
		color: $closeButtonOnDarkColor;
		fill: $closeButtonOnDarkColor;

		@include hoverEtc {
			color: $closeButtonOnDarkColor;
			fill: $closeButtonOnDarkColor;
		}
	}

	&--withText {
		width: auto;
	}

	&--absoluteRight {
		position: absolute;
		top: 0;
		right: $halfSpace;
	}

	&-text {
		@include accHide;

		&-show {
			color: $closeButtonForegroundColor;
			font-weight: bold;
			margin-right: 0.2rem;
		}
	}
}

.close-icon {
	width: 100%;
	height: 100%;
	fill: $closeButtonForegroundColor;
	pointer-events: all;

	.btn & {
		fill: inherit;
	}

	&--withText {
		width: 1.5168rem;
		height: 1.5168rem;
	}

}

.simpleClose-icon {
	width: 1.5rem;
	height: 1.5rem;
	@include theme((fill: foreground), #{&}, true);

	@include hoverEtc {
		transform: scale(1.1);
		transition: transform 400ms;
	}
}

@keyframes rotateUp {
	from, 50% {
		transform: rotateX(90deg);
	}
	to {
		transform: rotateX(0deg);
	}
}
@keyframes rotateDown {
	from, 50% {
		transform: rotateX(0deg);
	}
	to {
		transform: rotateX(90deg);
	}
}

.feedback-close,
.enquete-close,
.share-close,
.cookieAlert-close {
	$iconResize: 3 / 4;
	position: absolute;
	right: $halfSpace;
	transform-origin: 50% 100%;
	top: ($iconResize * $closeButtonSmallSize) * -1;
	padding: ($iconResize * $menuIconSizePaddingRatioSmall * $closeButtonSmallSize);
	width: $iconResize * $closeButtonSmallSize;
	height: $iconResize * $closeButtonSmallSize;
	background: getColor(donkerblauw default);

	&.isOpened {
		animation: rotateUp 1s;
		animation-fill-mode: forwards;
	}
	&.isClosed {
		animation: rotateDown 1s;
		animation-fill-mode: forwards;
	}

	&, .close-icon {
		color: getColor(wit);
		fill: getColor(wit);
	}

	@include mqGreaterThan(small) {
		top: ($iconResize * $closeButtonLargeSize) * -1;
		padding: ($iconResize * $menuIconSizePaddingRatioSmall * $closeButtonLargeSize);
		width: $iconResize * $closeButtonLargeSize;
		height: $iconResize * $closeButtonLargeSize;
	}

	@include hoverEtc {
		background: getColor(donkerblauw default);
		color: inherit;
		fill: inherit;
	}
}